html{font-size:62.5%}body{background-color:#14171b;font-family:Helvetica, Arial, sans-serif;font-size:1.6rem}button{cursor:pointer}button:disabled{cursor:default}.add-option-error{color:#b7dbff;font-style:italic;margin:1.6rem 0 0 0;padding:0 1.6rem}.add-option{display:flex;flex-direction:column;padding:1.6rem}.add-option__input{background:#14171b;border:none;border-bottom:0.3rem solid #000;color:#b7dbff;flex-grow:1;margin:0 0 1.2rem 0;padding:1.2rem}@media (min-width: 45rem){.add-option{flex-direction:row}.add-option__input{margin:0 1.2rem 0 0}}.big-button{background-color:#1e90ff;border:none;border-bottom:0.6rem solid #0077ea;color:#fff;font-weight:bold;font-size:3.2rem;width:100%;margin-bottom:1.6rem;padding:2.4rem}.big-button:disabled{opacity:.5}@media (min-width: 45rem){.big-button{margin-bottom:4.8rem}}.button{background-color:#1e90ff;border:none;border-bottom:0.3rem solid #0077ea;color:#fff;font-weight:500;padding:1.2rem}.button--link{background:none;border:none;color:#eaf5ff;padding:0}.container{max-width:60rem;margin:0 auto;padding:0 1.6rem}.header{background:#0D0F11;color:#fff;margin-bottom:1.6rem;padding:1.6rem 0}.header__title{font-size:3.2rem;margin:0}.header__subtitle{color:#b7dbff;font-size:1.6rem;font-weight:500;margin:0}@media (min-width: 45rem){.header{margin-bottom:4.8rem}}.ReactModalPortal>div{opacity:0}.ReactModalPortal .ReactModal__Overlay{align-items:center;display:flex;justify-content:center;transition:opcaity 200ms ease-in-out}.ReactModalPortal .ReactModal__Overlay--after-open{opacity:1}.ReactModalPortal .ReactModal__Overlay--before-close{opacity:0}.modal{background:#23282e;color:white;max-width:30rem;outline:none;padding:3.2rem;text-align:center}.modal__title{margin:0 0 1.6rem 0}.modal__body{font-size:2rem;font-weight:300;margin:0 0 3.2rem 0;word-break:break-all}.option{border-bottom:1px solid #39414b;display:flex;justify-content:space-between;padding:3.2rem 1.6rem}.option__text{color:white;font-weight:500;font-size:2rem;margin:0;word-break: